.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.navbar {
  width: 100%;
  background-color: rgba(107, 4, 64, 0.856);
  overflow: auto;
  font-family: "Comic Sans MS, Comic Sans, cursive";
}

.navbar a {
  float: left;
  padding: 5px;
  margin: 15px;
  color: rgb(240, 225, 241);
  text-decoration: none;
  display: flex;
  font-size: 50px;
  height: 50px;
  /* width: 25%; Four links of equal widths */
  text-align: center;
  font-family: "Comic Sans MS, Comic Sans, cursive";
}

.home-title {
  padding-top: 35px;
  padding-left: 110px;
  color: white;
  font-size: 70px;
  font-family: cursive;
}

.home-div {
  padding-top: 0px;
  padding-left: 75px;
  padding-right: 75px;
  color: white;
  font-size: 25px;
  font-family: "Comic Sans MS, Comic Sans, cursive";
}

.resume-pic {
  height: 100%;
  width: 100%;
  padding-bottom: 0;
}
.contact {
  background-color: rgb(219, 155, 219);
}
.live-coding {
  background-color: #db9bdb;
  display: inline-block;
}
.navbar a:hover {
  background-color: rgb(77, 3, 64);
}

.navbar a.active {
  background-color: #04aa6d;
}

.home {
  margin: 0;
  height: 100vh;
  font-weight: 100;
  background: radial-gradient(#a23982, #1f1013);
  -webkit-overflow-y: hidden;
  -moz-overflow-y: hidden;
  -o-overflow-y: hidden;
  overflow-y: hidden;
  -webkit-animation: fadeIn 1 1s ease-out;
  -moz-animation: fadeIn 1 1s ease-out;
  -o-animation: fadeIn 1 1s ease-out;
  animation: fadeIn 1 1s ease-out;
}

